*{
  padding:0px;
  margin:0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.head{
  font-weight: bolder;
  letter-spacing: 2px;
  background-color: black;
  color:white;
  padding:10px;
  text-align:left;
  position: relative;
}
.cart{
  font-size:30px;
 text-align: center;
}
i{
  position:relative;
  left:40%;
  
}
sup{
  
 font-size:15px;
 padding:4px;
 position: relative;
 bottom:10px;
 color:white;
 border-radius:50px;
 background-color: rgb(16, 54, 247);
}
img{
  height:200px;
}
.cart-wrapper{
  display:inline-flex;
 
  margin:40px;
  box-shadow:0 0 7px rgb(46, 46, 217);
  gap:10px;
}
.image-wrapper{
  width:180px;
  display:flex;
  align-items:center;
  justify-content: center;
  padding:20px;
  border-right:1px solid rgb(40, 34, 34);
}
.quantity{
  display:flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid gray;
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap:10px;
  padding:15px;
  text-align: center;
  display:relative;
  margin-top: 20px;
}

.btn{
  background-color: green;
  color:white;
  border-radius:3px;
  margin-bottom: 15px;
}
.price{
  margin-bottom: auto;
}
.quantity div{
  height:20px;
  width:20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity>div:first-child,
.quantity >div:last-child{
  background-color: blue;
  color: white;
}
.quantity > button:disabled{
  opacity: 0.7;
}
main{
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
 
}
.total{
   background-color: grey;
   text-align : right;
   padding: 10px;
   margin-top: -20px;
   
}